.subscription {
  position: relative;
  background-color: $bg-color-light-grey;

  &::after {
    position: absolute;
    content: "";
    right: 0;
    bottom: -94px;
    width: 321px;
    height: 384px;
    background-repeat: no-repeat;
    background-position: right;
    z-index: 0;
    background-image: image-set(url("../img/pancakes.webp") type("image/webp"),
      url("../img/pancakes.png") type("image/png"));
  }

  @include retina {
    &::after {
      width: 286px;
      background-image: image-set(url("../img/pancakes@2x.webp") type("image/webp"),
      url("../img/pancakes@2x.png") type("image/png"));
      background-size: cover;
    }
  }

  @include vp-1199 {
    padding: 104px 163px 100px;

    &::after {
      width: 348px;
      height: 395px;
      bottom: -101px;
      right: 0;
      background-image: image-set(url("../img/pancakes-tablet.webp") type("image/webp"),
      url("../img/pancakes-tablet.png") type("image/png"));
    }

    @include retina {
      &::after {
        background-image: image-set(url("../img/pancakes-tablet@2x.webp") type("image/webp"),
        url("../img/pancakes-tablet@2x.png") type("image/png"));
        background-size: cover;
      }
    }
  }

  @include vp-767 {
    padding: 66px 15px 70px;

    &::after {
      content: none;
      background: none;
    }
  }
}

.subscription__wrapper {
  position: relative;
  margin: 0 auto;
  padding: 126px 20px 120px;
  max-width: 1366px;
  width: 100%;
  z-index: 1;

  @include vp-1199 {
    padding: 0;
  }
}

.subscription__wrapper h3 {
  margin: 0;
  padding-bottom: 23px;

  @include h3-header;
  text-align: center;
}

.subscription__tabs {
  @media (min-width: $vp-1200) {
    margin: 0 auto;
    width: 1160px;
  }
}

.subscription__buttons {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 35px;

  &::after {
    position: absolute;
    content: "";
    bottom: -10px;
    left: 50%;
    width: 341px;
    height: 2px;
    background-color: rgba(120, 120, 120, 0.2);
    transform: translate(-50%, 0);
  }

  @include vp-1199 {
    margin-bottom: 20px;
    column-gap: 44px;

    &::after {
      width: 332px;
      bottom: -13px;
    }
  }

  @include vp-767 {
    margin-bottom: 49px;

    &::after {
      width: 290px;
      bottom: -15px;
    }
  }
}

.subscription__control.is-active {
  color: $color-primary;

  &::after {
    position: absolute;
    content: "";
    bottom: -11px;
    left: 7px;
    width: calc(100% - 12px);
    height: 2px;
    background-color: #d10a42;
  }

  @include vp-767 {
    &::after {
      left: -2px;
      width: calc(100% + 8px);
      bottom: -15px;
    }
  }
}

.subscription__control {
  position: relative;
  font-size: 20px;
  line-height: 150%;
  color: rgba(16, 38, 84, 0.5);
  border: none;
  outline: none;
  background-color: $color-transperent;

  @include hover-focus {
    color: $color-primary;

    &::after {
      position: absolute;
      content: "";
      bottom: -11px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #d10a42;
    }
  }

  @include active {
    color: $color-primary;

    &::after {
      position: absolute;
      content: "";
      bottom: -11px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #d10a42;
    }
  }

  @include vp-1199 {
    font-size: 18px;
  }

  @include vp-767 {
    padding: 0;
    font-size: 16px;

    @include hover-focus {
      &::after {
        bottom: -14px;
      }
    }
  }
}

.subscription__element {
  box-sizing: border-box;
  width: 1160px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: center;
  padding-top: 45px;


  @include vp-1199 {
    width: 100%;
    flex-direction: column;
    align-items: center;
    padding-top: 36px;
  }

  @include vp-767 {
    gap: 34px;
    padding-top: 0;
  }
}

.subscription__element-wrapper {
  box-sizing: border-box;
  position: relative;
  padding: 68px 75px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 360px;
  background-color: $color-white;
  z-index: 2;


  @include hover {

    padding-top: 61px;
    padding-bottom: 61px;
    border: 7px solid;
    border-image: $button-gradient;
    border-image-slice: 1;
  }

  @include vp-1199 {
    padding: 64px 77px 68px;
    width: 442px;
  }

  @include vp-767 {
    padding: 46px 40px 55px;
    width: 290px;

    @include hover-focus {
      padding-top: 39px;
      padding-bottom: 48px;
    }
  }
}

.subscription__element-wrapper h4 {
  @include h4-header;
  text-align: center;
  padding-bottom: 11px;
  margin: 0;

  @include vp-767 {
    margin-bottom: 6px;
    font-size: 22px;
    padding-bottom: 0;
  }
}

.subscription__element-wrapper > span {
  margin-bottom: 90px;
  display: block;
  font-weight: 400;

  @include text-regular;

  @include vp-767 {
    margin-bottom: 78px;
  }
}

.subscription__price-wrapper span {
  color: #e8e8e8;
  font-size: 100px;
  font-weight: 700;
  position: relative;
  margin-bottom: -56px;
  top: 0;
  left: 50%;
  width: auto;
  height: auto;
  transform: translate(-50%, -18px);

  @include vp-767 {
    font-size: 80px;
  }
}

.subscription__price-wrapper p {
  position: relative;
  margin: 0;
  margin-bottom: -6px;
  padding-right: 44px;
  text-align: center;
  color: $color-bright-blue;
  z-index: 1;
  top: 8px;
  font-size: 60px;
  font-weight: 700;
  line-height: 150%; /* 90px */

  &::after {
    position: absolute;
    content: "";
    top: 21px;
    right: 18px;
    width: 38px;
    height: 46px;
    background: url("../img/sprite/rubble.svg") no-repeat;
    background-size: cover;

    @include vp-767 {
      width: 36px;
      height: 33px;
      top: 19px;
      right: 10px;
    }
  }

  @include vp-767 {
    font-size: 48px;
    padding-bottom: 5px;
  }
}

.subscription__button {
  width: 206px;
  padding: 24px;
  text-align: center;
  text-transform: uppercase;
  color: $color-primary;
  border: 2px solid $color-primary;

  @include button-regular;

  &:hover,
  &:focus-visible {
    padding: 26px;
    color: $color-white;
    background: linear-gradient(to right, #ed0233, #003eb7);
    border: none;
    outline: none;
  }

  &:active {
    color: rgba(255, 255, 255, 0.7);
  }
}


.subscription__price-wrapper {
  position: relative;
  margin-bottom: 49px;
  display: flex;
  flex-direction: column;

  @include vp-767 {
    margin-bottom: 41px;
  }
}
